import { IsString, MinLength, ValidateNested } from 'class-validator';
import { Type } from 'class-transformer';

class ChangePasswordDto {
  @IsString()
  @MinLength(6, { message: '–°—Ç–∞—Ä—ã–π –ø–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 6 —Å–∏–º–≤–æ–ª–æ–≤' })
  oldPassword: string;

  @IsString()
  @MinLength(6, { message: '–ù–æ–≤—ã–π –ø–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 6 —Å–∏–º–≤–æ–ª–æ–≤' })
  newPassword: string;
}

export namespace AccountChangePasswordProfile {
  export const topic = 'account.change-password-profile.command';

  export class Request {
    @IsString()
    id: string;

    @Type(() => ChangePasswordDto)
    @ValidateNested()
    passwords: ChangePasswordDto;
  }

  export class Response {
    success: boolean;
  }
}
